.wp-block-capitainewp-css {
  background: #120F2C;
  padding: 40px 60px;
  border-radius: 10px;
}
.wp-block-capitainewp-css p.first-line {
  margin: 0;
  font-size: 5em;
  color: #D84989;
  font-weight: bold;
  line-height: 1.3;
  font-family: "Open Sans", sans-serif;
}
.wp-block-capitainewp-css h2 {
  color: #fff;
  font-size: 2.5em;
  line-height: 1.3;
  margin: 0;
  padding: 0;
}